import * as React from 'react'
import {Form, Field, reduxForm, InjectedFormProps} from 'redux-form';
import './TableForm.scss';

const TABLE_FORM_NAME = 'TABLE_FORM';

const TableForm = (props: any) =>  {

  const renderFormInput = (name: string, type: string) => {
    return (
      <div>
        <label>{name}</label>
        <input type={type} />
      </div>
    )
  }

    return (
      <Form onSubmit={props.handleSubmit}>
        <div className={"root"}>        
          <Field name="date" component={() => renderFormInput("Dtum", "text")} />
          <Field name="name" component={() => renderFormInput("Nv", "text")} />
          <Field name="address" component={() => renderFormInput("Lakcm", "text")} />
          <Field name="taxID" component={() => renderFormInput("AdSzm", "text")} />
          <Field name="originID" component={() => renderFormInput("Szrmazsi Igazolvny Szm", "text")} />
          <Field name="HLF" component={() => renderFormInput("Hektoliterfok", "text")} />
          <Field name="weightInKilograms" component={() => renderFormInput("Tmeg", "text")} />
          <button onClick={props.handleSubmit}>Klds</button>
        </div>
      </Form>
    )
  }

export default reduxForm({
    form: TABLE_FORM_NAME
})(TableForm)